@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,300;1,100;1,400&display=swap);


.navbar {
    padding: 1.8rem 0;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px;
}

.navbar-brand {
    width: 90px;
}

.navbar-brand span {
    margin-left: 2rem;
}

.brand-and-toggler {
    width: 100%;
}

.navbar-content {
    display: flex;
    width: 100%;
}

.navbar-toggler-btn {
    transition: var(--transition);
    z-index: 11;
}

.navbar-collapse {
    position: fixed;
    right: 0;
    top: 0;
    z-index: 10;
    background-color: var(--black-color);
    height: 100%;
    width: 280px;
    display: flex;
    padding: 10rem 3rem 0 3rem;
    -webkit-transform: translatex(100%);
            transform: translatex(100%);
    transition: var(--transition);
}

.show-navbar-collapse {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.nav-item {
    margin-bottom: 1.4rem;
}

.nav-link {
    transition: var(--transition);
}

.nav-link:hover {
    color: var(--purple-color);
}

@media screen and (min-width: 992px) {
    .navbar-collapse {
        position: relative;
        height: auto;
        padding: 0;
        width: 100%;
        background-color: transparent;
        -webkit-transform: translateX(0);
                transform: translateX(0);
        justify-content: flex-end;
    }

    .navbar-toggler-btn {
        display: none;
    }

    .nav-item {
        margin-bottom: 0;
        margin-left: 2.8rem;
    }

    .nav-link {
        color: var(--black-color);
        font-size: 1.7rem;
        font-weight: 500 !important;
        opacity: 0.8;
    }

    .navbar-nav {
        display: flex;
    }
}
.search-form {
    width: 100%;
    max-width: 680px;
}

.search-form-elem {
    padding: 1.5rem 2.9rem;
    border-radius: 3.5rem;
}

.search-form-elem .form-control {
    font-size: 1.5rem;
    padding: 0.7rem;
}

.search-form-elem .form-control::-webkit-input-placeholder {
    opacity: 0.9;
}

.search-form-elem .form-control::placeholder {
    opacity: 0.9;
}
.header-content {
    min-height: 75vh;
    background: linear-gradient(rgba(141, 39, 174, 0.3), rgba(141, 39, 174, 0.5)), center/cover no-repeat;
    flex-direction: column;
}

.header-title {
    margin-top: -1.5rem;
    margin-bottom: 2.5rem;
    opacity: 0.8;
    max-width: 770px;
}


.header-text {
    font-size: 4.5rem;
}
.about{
    padding: 3rem 0;
    background-image:src='src/images/bookShelf.jpg' ;
}
.about-content{
    grid-gap: 5rem;
    gap: 5rem;
}
.about-title{
    margin: 1rem 0;
}
.about-img img{
    max-width: 520px;
}
.about-text p{
    margin: 2.0rem 0;
    opacity: 0.8;
}

@media screen and (min-width: 800px){
    .about-content{
        grid-template-columns: repeat(2, 1fr);
    }
    .about-title{
        margin-top: 0;
    }
    .about-img img{
        margin-left: 0;
    }
}
.booklist,
.book-details {
    padding: 3rem 0;
    background-color: #f8f9fa;
}

.booklist-content {
    grid-gap: 3rem;
    gap: 3rem;
}

.book-item {
    padding: 3rem 2rem;
    border-radius: 4px;
    background-color: var(--white-color);
    box-shadow: rgba(0, 0, 0, 0.05) 0px 5px 15px 0px;
    transition: var(--transition);
}

.book-item:hover {
    box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px;
}

.book-item-img img {
    max-width: 180px;
    margin-right: auto;
    margin-left: auto;
}

.book-item-info {
    margin-top: 2rem;
}

.book-item-info-item.title {
    line-height: 1.4;
    margin-bottom: 0.8rem;
}

.book-item-info-item.author {
    line-height: 1.4;
    margin-bottom: 4px;
}

.book-item-info-item:not(.book-item-info-item.title) span:last-child {
    opacity: 0.8;
}

.book-item-info-item.publish-year {
    opacity: 0.6;
    font-style: italic;
    font-size: 14px;
}

@media screen and (min-width: 600px) {
    .booklist-content {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media screen and (min-width: 992px) {
    .booklist-content {
        grid-template-columns: repeat(3, 1fr);
    }

    .book-item-img img {
        max-width: 150px;
    }
}

@media screen and (min-width: 992px) {
    .booklist-content {
        grid-template-columns: repeat(4, 1fr);
    }

    .book-item-img img {
        max-width: 120px;
    }
}
.loader {
    padding: 5rem 0;
}

.loader img {
    width: 120px;
}
.book-details {
    padding: 6rem 0;
}

.back-btn {
    margin-left: 25px;
    margin-bottom: 2.6rem;
    transition: var(--transition);
}

.back-btn:hover {
    color: var(--purple-color);
}

.back-btn span {
    margin-left: 1rem;
}

.book-details-content {
    grid-gap: 4rem;
    gap: 4rem;
}

.book-details-img {
    max-height: 600px;
    overflow: hidden;
}

.book-details-img img {
    margin: 0 auto;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.book-details-item {
    margin-bottom: 1.4rem;
}

.book-details-item.description {
    opacity: 0.8;
}

.book-details-info {
    overflow-y: scroll;
    max-height: 600px;
    padding: 1.4rem;
}

.book-details-info::-webkit-scrollbar {
    width: 8px;
}

.book-details-info::-webkit-scrollbar-track {
    border-radius: 8px;
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

.book-details-info::-webkit-scrollbar-thumb {
    background-color: var(--purple-color);
    outline: -1px solid slategray;
    border-radius: 8px;
}

@media screen and (min-width: 768px) {
    .book-details-content {
        grid-template-columns: 40% 60%;
        grid-gap: 0;
        gap: 0
    }

    .book-details-img {
        padding: 0 5rem 0 2rem;
    }

    .book-details-img img {
        max-width: 100%;
    }
}
